@import "https://fonts.googleapis.com/css2?family=Forum&display=swap";
@import "https://fonts.cdnfonts.com/css/berold";
@import "https://fonts.googleapis.com/css2?family=Manrope:wght@300;400&display=swap";
:root {
  --header-height: 3rem;
  --first-color: #01161e;
  --first-color-dark: #0c0f0a;
  --text-color: #524748;
  --first-color-light: #7b6f71;
  --first-color-lighten: #fbf9f9;
  --bg-color: #00121ad2;
  --body-font: "Manrope", sans-serif;
  --biggest-font-size: 2.5rem;
  --h1-font-size: 1.5rem;
  --h2-font-size: 1.25rem;
  --h3-font-size: 1.125rem;
  --normal-font-size: .938rem;
  --small-font-size: .813rem;
  --smaller-font-size: .75rem;
  --font-medium: 500;
  --font-semi-bold: 600;
  --font-bold: 700;
  --mb-1: .5rem;
  --mb-2: 1rem;
  --mb-3: 1.5rem;
  --mb-4: 2rem;
  --mb-5: 2.5rem;
  --mb-6: 3rem;
  --z-normal: 1;
  --z-tooltip: 10;
  --z-fixed: 100;
  --shadow: #00000014 0px 6px 10px 0px, #00000026 0px 0px 0px 1px;
  --gap: .5rem;
  --radius: 5px;
}

@media screen and (width >= 768px) {
  :root {
    --biggest-font-size: 4.5rem;
    --h1-font-size: 2.25rem;
    --h2-font-size: 1.5rem;
    --h3-font-size: 1.25rem;
    --normal-font-size: 1rem;
    --small-font-size: .875rem;
    --smaller-font-size: .813rem;
  }
}

*, :before, :after {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  margin: var(--header-height) 0 0 0;
  font-family: var(--body-font);
  font-size: 1.1rem;
  font-weight: var(--font-medium);
  background-color: var(--first-color-lighten);
  color: var(--text-color);
  line-height: 1.6;
  position: relative;
  overflow-x: hidden;
}

.modal {
  background-color: var(--bg-color);
  z-index: 1111111;
  width: 100%;
  height: 100vh;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.modal__container {
  color: var(--first-color);
  background-color: var(--first-color-lighten);
  height: 100vh;
}

.modal__header {
  background-color: var(--bg-color);
  color: var(--first-color-lighten);
  justify-content: space-between;
  align-items: stretch;
  display: flex;
}

.modal__header ul {
  justify-content: space-between;
  display: flex;
}

.modal__header ul li {
  cursor: pointer;
  text-align: center;
  align-items: center;
  padding: 0 2.2rem;
  display: flex;
}

.active__li {
  background-color: var(--first-color-lighten);
  color: var(--first-color);
  border-radius: 70% 24% 0% 100% / 71% 100% 17% 0%;
  transition: background-color .3s;
}

.modal__exit {
  cursor: pointer;
  align-self: center;
  padding: .6rem 0 .6rem 2.4rem;
  display: flex;
}

.modal__body {
  flex-direction: column;
  gap: 1.8rem;
  padding: 1.5rem 5rem;
  display: flex;
}

.modal__p {
  text-align: justify;
}

.modal__dropdownCont {
  display: none;
}

.btn {
  cursor: pointer;
  box-shadow: var(--shadow);
  background-color: #fff;
  border: none;
  justify-content: flex-start;
  align-items: center;
  padding: .6rem;
  display: flex;
  position: relative;
}

.bx {
  font-size: 1.1rem;
}

.dropdown {
  box-shadow: var(--shadow);
  visibility: hidden;
  opacity: 0;
  background: #fff;
  width: max-content;
  margin-top: 1rem;
  transition: all .1s cubic-bezier(.16, 1, .5, 1);
  position: absolute;
  transform: translateY(.5rem);
}

.dropdown a {
  color: #000;
  align-items: center;
  gap: .5rem;
  padding: .8rem 1rem;
  text-decoration: none;
  display: flex;
}

.dropdown a:hover {
  background-color: var(--color);
  color: #fff;
}

.show {
  visibility: visible;
  opacity: 1;
  transform: translateY(0);
}

.arrow {
  transition: all .2s;
  transform: rotate(180deg);
}

@media screen and (width <= 805px) {
  .modal__exit {
    width: 7rem;
    height: 3.5rem;
  }
}

@media screen and (width <= 677px) {
  .modal {
    font-size: .85rem;
  }

  .modal__header ul li {
    padding: .2rem 1.2rem;
  }

  .active__li {
    border-top-left-radius: 1.5rem;
    border-top-right-radius: 1.5rem;
  }

  .modal__exit {
    width: 4rem;
    height: 2rem;
    padding: .2rem 1.2rem;
  }

  .modal__body {
    padding: 1.5rem 2rem;
  }
}

@media screen and (width <= 450px) {
  .modal {
    font-size: .8px;
  }

  .modal__header {
    padding: 1rem 0;
  }

  .modal__header ul {
    display: none;
  }

  .modal__dropdownCont {
    display: block;
  }
}

h1, h2, h3, ul, p {
  margin: 0;
}

h2, h3 {
  font-weight: var(--font-semi-bold);
}

ul {
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.swiper__carousel {
  align-self: center;
  height: 65%;
}

.swiper__slider img {
  width: 100%;
  height: 100%;
}

.section {
  padding: 4rem 0 2rem;
}

.section-title, .section-subtitle {
  text-align: center;
}

.section-title {
  font-size: var(--h1-font-size);
  color: var(--first-color);
  margin-bottom: var(--mb-3);
}

.section-subtitle {
  font-weight: var(--smaller-font-size);
  font-weight: var(--font-semi-bold);
  display: block;
}

.bd-container {
  margin-left: var(--mb-2);
  margin-right: var(--mb-2);
  width: calc(100% - 2rem);
  max-width: 1024px;
}

.bd-grid {
  gap: 1.5rem;
  display: grid;
}

.l-header {
  z-index: var(--z-fixed);
  background-color: #000;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
}

.nav {
  height: var(--header-height);
  justify-content: space-between;
  align-items: center;
  display: flex;
}

@media screen and (width <= 768px) {
  .nav__menu {
    text-align: center;
    background-color: #000;
    border-radius: 0 0 1rem 1rem;
    width: 100%;
    padding-top: 1.5rem;
    transition: all .4s;
    position: fixed;
    top: -100%;
    left: 0;
    box-shadow: 0 2px 4px #0000001a;
  }
}

.nav__item {
  margin-bottom: var(--mb-3);
}

.nav__link {
  color: var(--first-color-lighten);
  transition: all .3s;
}

.nav__link:hover {
  color: var(--first-color-light);
}

.nav__logo, .nav__toggle {
  color: var(--first-color-lighten);
}

.nav__logo {
  width: 2rem;
  height: 2rem;
}

.nav__logo img {
  width: 100%;
}

.footer img {
  width: 2rem;
  height: 2rem;
  margin: 1rem auto;
}

.footer__social {
  justify-content: center;
  display: flex;
}

.nav__toggle {
  cursor: pointer;
  font-size: 1.3rem;
}

.show-menu {
  top: var(--header-height);
}

.active-link {
  position: relative;
}

.active-link:after {
  content: "";
  background-color: var(--first-color-light);
  width: 100%;
  height: 3px;
  position: absolute;
  bottom: -.7rem;
  left: 0;
}

.scroll-header {
  background-color: var(--first-color-lighten);
  box-shadow: 0 2px 4px #0000001a;
}

.scroll-header .nav__logo, .scroll-header .nav__toggle, .scroll-header .nav__link {
  color: var(--first-color-dark);
}

.scroll-header .nav__menu {
  background-color: var(--first-color-lighten);
}

.scrolltop {
  z-index: var(--z-tooltip);
  background: #7b6f71b3;
  border-radius: .5rem;
  justify-content: center;
  align-items: center;
  padding: .5rem;
  transition: all .4s;
  display: flex;
  position: fixed;
  bottom: -20%;
  right: 1rem;
}

.scrolltop:hover {
  background-color: var(--first-color);
}

.scrolltop__icon {
  color: var(--first-color-lighten);
  font-size: 2rem;
}

.show-scroll {
  visibility: visible;
  bottom: 1.5rem;
}

.home {
  background-image: url("background.4e5ad45d.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  overflow: hidden;
}

.home__container {
  height: calc(100vh - var(--header-height));
  grid-template-rows: repeat(2, max-content);
  align-content: space-around;
  row-gap: 2rem;
  position: relative;
}

.home__data {
  border-left: 4px solid var(--first-color-lighten);
  color: var(--first-color-lighten);
  z-index: var(--z-tooltip);
  padding-left: 1.5rem;
}

.home__name {
  text-transform: lowercase;
  font-family: Berold, sans-serif;
  font-size: 4.5rem;
  font-weight: 100;
}

.home__greeting, .home__profession {
  font-weight: var(--font-bold);
  display: block;
}

.home__greeting span {
  font-size: 2.6rem;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateY(-4rem);
}

.home__greeting {
  font-size: 1rem;
  font-weight: 100;
}

.home__profession {
  margin-bottom: var(--mb-3);
  font-size: 1rem;
  font-weight: 100;
}

.home__img {
  position: absolute;
  bottom: 0;
  right: 0;
}

.home__img img {
  width: 240px;
}

.home__social {
  flex-direction: column;
  display: flex;
}

.home__social-icon {
  margin-bottom: var(--mb-2);
  color: var(--first-color-lighten);
  width: max-content;
  font-size: 1.3rem;
}

.home__social-icon:hover {
  color: var(--first-color-light);
}

.button {
  background-color: var(--first-color);
  color: var(--first-color-lighten);
  border-radius: .25rem;
  padding: .75rem 1rem;
  transition: all .3s;
  display: inline-block;
}

.button:hover {
  background-color: var(--first-color-dark);
}

.button-light {
  background-color: var(--first-color-light);
}

.button-white {
  background-color: var(--first-color-lighten);
  color: var(--first-color-dark);
}

.button-white:hover {
  background-color: var(--first-color-lighten);
}

.button-link {
  color: var(--first-color);
  background: none;
  padding: 0;
}

.button-link:hover {
  background: none;
}

.about__data {
  text-align: justify;
}

.about__description span {
  font-size: var(--h2-font-size);
  font-weight: var(--font-semi-bold);
  color: var(--first-color);
}

.about__number {
  font-size: var(--h1-font-size);
  color: var(--first-color);
  display: block;
}

.about__num {
  text-align: center;
}

.about__img {
  border-radius: .5rem;
  place-self: center;
  width: 220px;
}

.qualification__container {
  row-gap: 2.5rem;
}

.qualification__title {
  font-size: var(--h3-font-size);
  color: var(--first-color);
  margin-bottom: var(--mb-2);
  align-items: center;
  display: flex;
}

.qualification__title-icon {
  margin-right: var(--mb-1);
  font-size: 1.5rem;
}

.qualification__box {
  border-bottom: 1px solid #ccc;
  padding-bottom: 1rem;
}

.qualification__area {
  font-size: var(--normal-font-size);
  color: var(--first-color);
  margin-bottom: var(--mb-1);
  font-weight: var(--font-medium);
}

.qualification__icon, .qualification__work {
  font-size: var(--smaller-font-size);
  color: var(--first-color-light);
}

.qualification__work {
  display: block;
}

.services {
  position: relative;
}

.services__data {
  text-align: center;
  background-color: #fff;
  border-radius: .5rem;
  padding: 3rem 1rem 5rem;
  transition: all .4s;
  position: relative;
  box-shadow: 0 4px 6px #aebecd4d;
}

.services__btn {
  cursor: pointer;
  position: absolute;
  bottom: 30px;
  left: 50%;
  transform: translateX(-50%);
}

.services__icon, .services__title {
  margin-bottom: var(--mb-2);
  color: var(--first-color);
}

.services__icon {
  font-size: 3rem;
}

.services__title {
  font-size: var(--h3-font-size);
}

.services__description {
  margin-bottom: var(--mb-4);
}

.services__data:hover {
  transform: translateY(-.5rem);
  box-shadow: 0 6px 8px #aebecd66;
}

.project {
  background-image: url("background2.68139903.jpg");
  background-position: 0 82%;
  background-repeat: no-repeat;
  background-size: cover;
  margin-top: 4rem;
  position: relative;
}

.project:after {
  content: "";
  opacity: .4;
  background-color: #011e2a;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.project__cover {
  padding: 3.6rem;
}

.slider__container {
  background-color: var(--bg-color);
  z-index: 11111111;
  flex-direction: column;
  width: 100%;
  height: 100%;
  display: none;
  position: fixed;
  top: 0;
  left: 0;
}

.slider__images {
  justify-content: center;
  align-items: center;
  gap: 2rem;
  height: 90%;
  display: flex;
  position: relative;
}

.slider__images img {
  max-height: 90%;
  box-shadow: 0 0 50px -3px #00000080;
}

.slider__main swiper-slide {
  justify-content: center;
  align-self: center;
  display: flex;
}

.slider__exit {
  justify-content: end;
  align-items: center;
  width: 100%;
  padding: 1rem 1rem 0 0;
  display: flex;
}

.slider__arrow {
  background-color: var(--first-color-lighten);
  cursor: pointer;
  border-radius: 50%;
  padding: .5rem;
  transition: transform .3s;
  position: absolute;
  top: 50%;
  box-shadow: .1px .1px 40px .5px #000;
}

.slider__arrow-left:hover, .slider__arrow-left:active {
  transition: transform .3s;
  transform: translateX(-5px)translateY(-50%)rotate(180deg);
}

.slider__arrow-right:hover, .slider__arrow-right:active {
  transition: transform .3s;
  transform: translateX(5px)translateY(-50%);
}

.slider__arrow-left {
  left: 0;
  transform: translateY(-50%)rotate(180deg);
}

.slider__arrow-right {
  right: 0;
  transform: translateY(-50%);
}

.slider__exit_icon:hover {
  cursor: pointer;
}

.portfolio__nav {
  text-align: center;
  margin-bottom: var(--mb-3);
}

.portfolio__item {
  margin: 0 var(--mb-2);
  cursor: pointer;
}

.portfolio__content {
  background-color: #fff;
  border-radius: .5rem;
  height: max-content;
  overflow: hidden;
  box-shadow: 0 4px 6px #aebecd4d;
}

.portfolio__img {
  width: 100%;
  max-height: 40%;
  transition: all .4s;
}

.portfolio__data {
  padding: 1.5rem;
}

.portfolio__subtitle {
  font-size: var(--smaller-font-size);
  color: var(--first-color-light);
}

.portfolio__title {
  font-size: var(--h3-font-size);
  color: var(--first-color);
  margin: var(--mb-2) 0;
}

.portfolio__content:hover {
  box-shadow: 0 6px 8px #aebecd66;
}

.portfolio__content:hover .portfolio__img {
  transform: scale(1.02);
}

.active-portfolio {
  color: var(--first-color-dark);
  font-weight: var(--font-semi-bold);
}

.testimonial__content {
  background-color: var(--first-color-dark);
  color: var(--first-color-lighten);
  text-align: center;
  grid-gap: 1.5rem;
  border-radius: .5rem;
  padding: 2rem 1rem;
  display: grid;
  box-shadow: 0 4px 8px #2f0a0d40;
}

.testimonial__otziv {
  justify-content: center;
  margin-top: 4rem;
  display: flex;
}

.testimonial__form {
  grid-template-rows: max-content 1fr max-content max-content;
  grid-template-columns: repeat(1, 1fr);
  grid-template-areas: "name"
                       "message"
                       "rate"
                       "submit";
  display: grid;
}

.testimonial__form div {
  grid-area: rate;
}

.testimonial__form div input[type="number"]::-webkit-inner-spin-button {
  appearance: none;
}

.testimonial__form div input[type="number"]::-webkit-outer-spin-button {
  appearance: none;
}

#name {
  grid-area: name;
}

#message {
  text-align: start;
  grid-area: message;
}

#submit {
  grid-area: submit;
  justify-self: flex-start;
}

.testimonial__button {
  background-color: var(--first-color);
  color: var(--first-color-lighten);
  font-family: var(--body-font);
  font-size: var(--normal-font-size);
  border-radius: .25rem;
  outline: none;
  padding: .75rem 1rem;
  transition: all .3s;
  display: inline-block;
}

.testimonial__star {
  color: gold;
}

.swiper-pagination {
  position: initial;
}

.swiper-pagination-bullet-active {
  background-color: var(--first-color);
}

.contact__container {
  row-gap: 2.5rem;
}

.contact__content {
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
}

.contact__box {
  text-align: center;
  background-color: #fff;
  border-radius: .5rem;
  padding: 1.5rem;
  box-shadow: 0 4px 6px #aebecd4d;
}

.contact__icon, .contact__title {
  color: var(--first-color);
}

.contact__icon {
  font-size: 2rem;
}

.contact__title {
  font-size: var(--h3-font-size);
  margin: var(--mb-1) 0;
}

.contact__social {
  color: var(--first-color-light);
  margin: 0 var(--mb-1);
  font-size: 1.25rem;
}

.contact__box:hover {
  box-shadow: 0 6px 8px #aebecd66;
}

.contact__inputs {
  grid-template-columns: repeat(2, 1fr);
  column-gap: 1rem;
  display: grid;
}

.contact__input, .contact__button {
  font-family: var(--body-font);
  font-size: var(--normal-font-size);
  outline: none;
}

.contact__input {
  border: 2px solid var(--first-color-light);
  color: var(--first-color);
  margin-bottom: var(--mb-1);
  border-radius: .5rem;
  width: 100%;
  padding: 1rem;
}

.contact::placeholder {
  color: var(--first-color-light);
  font-family: var(--body-font);
  font-weight: var(--font-semi-bold);
}

.contact__button {
  cursor: pointer;
  border: none;
}

.footer {
  color: var(--first-color-lighten);
  text-align: center;
  position: relative;
}

#particles-js {
  z-index: -1;
  background-color: #000;
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  height: 100%;
  padding-bottom: 1rem;
  position: absolute;
  top: 0;
  left: 0;
}

.footer__container {
  z-index: 1111;
  padding: 3rem 0;
}

.footer__title {
  margin-bottom: var(--mb-1);
  font-family: Berold, sans-serif;
  font-size: 2.4rem;
  font-weight: var(--font-semi-bold);
  text-transform: lowercase;
  font-weight: 100;
}

.footer__description {
  margin-bottom: var(--mb-6);
  font-size: 1.3rem;
  font-weight: 100;
}

.footer__link {
  color: var(--first-color-lighten);
  margin: 0 var(--mb-1);
  font-size: 1.4rem;
  transition: all .3s;
}

.footer__link:hover {
  color: var(--first-color-light);
}

.footer__copy {
  font-size: var(--smaller-font-size);
  color: var(--first-color-light);
}

@media screen and (width >= 576px) {
  .home__img img {
    width: 330px;
  }

  .about__container {
    flex-direction: column;
    display: flex;
  }

  .swiper__carousel {
    width: 100%;
    padding-top: 50px;
    padding-bottom: 50px;
  }

  .swiper__slider {
    background-position: center;
    background-size: cover;
    width: 300px;
    height: 300px;
  }

  .swiper__slider img {
    width: 100%;
    height: 100%;
    display: block;
  }

  .qualification__container, .services__container, .portfolio__container {
    grid-template-columns: repeat(2, 1fr);
  }

  .about__container {
    gap: 5rem;
  }

  .contact__form {
    justify-content: center;
    width: 450px;
  }
}

@media screen and (width >= 768px) {
  body {
    margin: 0;
  }

  .section {
    padding-top: 6rem;
  }

  .section-title {
    margin-bottom: var(--mb-5);
  }

  .nav {
    height: calc(var(--header-height)  + 1.5rem);
  }

  .nav__list {
    display: flex;
  }

  .nav__item {
    margin-left: var(--mb-5);
    margin-bottom: 0;
  }

  .nav__toggle {
    display: none;
  }

  .home__container {
    grid-template-rows: max-content .5fr;
    align-content: flex-end;
    height: 100vh;
  }

  .home__greeting {
    font-size: 1.25rem;
  }

  .home__profession {
    font-size: 2rem;
  }

  .home__social {
    flex-direction: row;
